/*global __PREFIX_PATHS__, __PATH_PREFIX__ */
import React from 'react'
import Link, { withPrefix } from 'gatsby-link'
import Nav from '../components/Nav'
import '../../static/main.scss'
import { getPageSpecificClass } from '../utils/getDataFromPath'

import { rhythm, scale } from '../utils/typography'

class Template extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      typistClassName: 'typist-incomplete',
      headerClassName: 'header_home',
      isHome: true
    }
    this.onTypistStateChange = this.onTypistStateChange.bind(this)
    this.onChildMounted = this.onChildMounted.bind(this)
    this.updateHomeState = this.updateHomeState.bind(this)
    this.getIsHome = this.getIsHome.bind(this)
  }
  onTypistStateChange(complete = true) {
    console.debug(`onTypistComplete called`)
    const newTypistClassName = (complete || this.getIsHome()) ? 'typist-complete' : 'typist-incomplete'
    this.setState((prevState) => {
      return { ...prevState, typistClassName: newTypistClassName }
    })
  }
  getIsHome() {
    let rootPath = `/`
    if (withPrefix) {
      rootPath = withPrefix(rootPath)
    }
    const _isHome = rootPath === this.props.location.pathname
    console.debug(`isHome: ${_isHome}; location.pathname: ${this.props.location.pathname}; rootPath: ${rootPath}`)
    return _isHome
  }
  updateHomeState(setHome = false) {
    const isHome = setHome ? setHome : this.getIsHome()
    const headerClassName = isHome ? 'header_home' : 'header_page'
    const typistClassName = isHome ? 'typist-incomplete' : 'typist-complete'
    this.setState(prevState => {
      return { ...prevState, typistClassName, isHome, headerClassName }
    })
  }
  componentDidMount() {
    // this._isMounted = true
    this.updateHomeState()
  }
  onChildMounted() {
    console.debug(`onChildMounted called`)
    this.updateHomeState()
  }
  render() {
    const { location, children, data: layoutData } = this.props
    if (!layoutData) {
      return <p>Nothing here!</p>
    }
    const navPaths = layoutData.pages.edges.map(edge => edge.node.path)
    //console.log('navPaths', navPaths)
    const navData = layoutData.pagesMarkdown.edges.map(edge => {
      const path = navPaths.filter(
        navPath =>
          edge.node.fileAbsolutePath.indexOf(navPath) !== -1 && navPath !== '/'
      )[0]
      const title = edge.node.frontmatter.navTitle
        ? edge.node.frontmatter.navTitle
        : edge.node.frontmatter.title
      return {
        title,
        path,
      }
    })

    const pageLayoutClass = getPageSpecificClass(location)

    const homeHeader = (
      <div className={`header ${this.state.headerClassName}`}>
        <Nav items={navData} classNames="header__nav" />
      </div>
    )

    const pageHeader = (
      <div className={`header ${this.state.headerClassName}`}>
        <h3
          style={{
            fontFamily: 'Montserrat, sans-serif',
            marginTop: 0,
            marginBottom: rhythm(-1),
          }}
        >
          <Link
            style={{
              boxShadow: 'none',
              textDecoration: 'none',
              color: 'inherit',
            }}
            to={'/'}
            onClick={ 
              () => {
                this.onTypistStateChange(false)
                this.updateHomeState(true)
              } 
            }
          >
            {layoutData.site.siteMetadata.title}
          </Link>
        </h3>
        <Nav items={navData} classNames="header__nav" />
      </div>
    )

    return (
      <main
        className={`layout main layout_${pageLayoutClass} ${this.state.typistClassName}`}
        style={{
          maxWidth: rhythm(24),
          minWidth: rhythm(14),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
        }}
      >
        {this.state.isHome ? homeHeader : pageHeader}
        {children({ ...this.props, onTypistComplete:this.onTypistStateChange, onChildMounted: this.onChildMounted })}
      </main>
    )
  }
  
  
}

export default Template

// Return markdown title and navTitle for pages only, and not the root index page, along with page paths
// Working exclusion regex : regex: "/^(?!.*(\/(posts|partials)))/",
// but with reorgz, should be able to do inclusion of pages only
export const templateQuery = graphql`
  query TemplateQuery {
    pagesMarkdown: allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "//pages/", ne: "/" } }
    ) {
      edges {
        node {
          fileAbsolutePath
          frontmatter {
            title
            navTitle
          }
        }
      }
    }
    pages: allSitePage {
      edges {
        node {
          path
        }
      }
    }
    site {
      siteMetadata {
        title
      }
    }
  }
`



